.aboutactive {
	padding: 20px 20px 56px;
	margin-top: 20px;
	margin-bottom: 20px;
	position: relative;
	.date {
		display: block;
		position: absolute;
		width: 52px;
		height: 52px;
    background: $active-font-color;
    left: -56px;
    top: 0;
    padding:2px 2px;
    span {
    	display: block;
    	color: #fff;
    	line-height: 18px;
    	font-size: 12px;
    	margin-top: 4px;
    }
	}
	&:last-child {
		margin-bottom: 0;
		padding-bottom: 2px;
	}
	h3 {
		margin-bottom: 0;
		font-size: 14px;
	}
	time {
		font-size: 10px;
	}
	.img-wrap{
    img {
    width: initial;
    }
	}
	.poem {
		.para {
			line-height: 22px;
		}
		+ .poem {
			margin-top: 40px;

		}
	}
}
.businesscard {
	padding: 20px 20px 56px;
	margin-top: 20px;
	margin-bottom: 20px;
	.img-wrap{
    img {
    width: initial;
    }
	}
}
.limited-height {
	  max-height: 22px;
    overflow: hidden;
	}